<template>
  <Card class="shadow-lg h-full">
    <div class="flex items-center">
      <div class="flex-grow">
        <div class="flex items-center flex-wrap">
          <p class="font-semibold mr-4 mb-1">{{ orderTitle }}</p>
          <span v-if="state.total_orders" class="p-2 w-2 h-2 text-xs flex items-center justify-center box-content bg-teal-04 text-white rounded-full">{{ state.total_orders >= 99 ? '99+' : state.total_orders }}</span>
        </div>
      </div>
      <div class="flex-grow flex justify-end">
        <div class="block">
            <Button :disabled="state.orders?.length < 1" color="teal" link :to="{ name: 'buybackOnlineTab', params: { category: 'active', statusId: '2' } }">
            <i class="material-icons text-sm align-text-middle mr-2">visibility</i> View all
          </Button>
        </div>
      </div>
    </div>
    <hr class="border-grey-01 my-6">
    <template v-if="state.orders.length !== 0">
      <div v-for="(order, index) in state.orders" class="flex justify-between items-center " :key="index">
        <router-link class="w-full cursor-pointer flex justify-between transition duration-200 hover:bg-grey-lighten p-3" :to="{ name: buyback_type === 3 ? 'buybackLocalDetail' : 'buybackOnlineDetail', params: { buybackId: order.box_id } }">
          <div class="flex flex-row items-center">
            <div>
              <TextLimit :text="order.name ? order.name : order.email">
                <template #default="scoped">
                  <b class="block text-teal-04 mb-2">{{ scoped.text }}</b>
                </template>
              </TextLimit>
              <p class="text-dark text-xs">{{ order.created_at }}</p>
            </div>
          </div>
          <div class="text-right"><b class="text-black">
            <Currency :value="parseFloat(order.sub_total).toFixed(2).toLocaleString()" />
          </b>
          </div>
        </router-link>
      </div>
    </template>
    <div v-else class="text-center">
      <p class="font-bold text-red mb-0">Nothing to see here</p>
    </div>
  </Card>
</template>

<script>
import { defineComponent } from "vue";
import { useOrders } from "composable/dashboard";
import Card from "bbUi/components/card/card.vue";
import CardHeader from "components/dashboard/CardHeader.vue";
import Button from 'bbUi/components/button/button.vue'
import Currency from 'components/Currency.vue'
import TextLimit from 'components/TextLimit.vue'

export default defineComponent({
  components: {
    Card,
    CardHeader,
    Button,
    Currency,
    TextLimit
  },
  props: {
    statusId: {
      type: Number,
      required: true,
    },
    orderTitle: {
      type: String,
      required: true,
    },
    buyback_type: {
      type: [String, Number],
      default: 2
    }
  },
  async setup(props) {
    const { data, state, arrivingToday } = useOrders();


    await arrivingToday();

    return {
      data,
      state,
    };
  },
});
</script>